@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap";
*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  user-select: none;
  background-color: #f0f0f0;
  font-family: Roboto, sans-serif;
  position: relative;
}

#WithoutuseState {
  background-color: #010100;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 10px;
  flex-direction: column;
  gap: 20px;
  width: min(90%, 900px);
  display: flex;
}

.heading {
  color: #f7e7ce;
  width: max-content;
  padding: 20px;
  font-family: Roboto, sans-serif;
  font-size: 2.5rem;
}

.changecolorbox1 {
  color: #fff;
  background-color: #d9d9d9;
  border-radius: 0 100px 100px 0;
  width: 70%;
  padding: 40px 0;
}

.changecolorbox1 span {
  color: #010100;
  margin-left: 10px;
  font-size: 1.5rem;
}

.btn {
  cursor: pointer;
  border-radius: 50%;
  justify-content: center;
  align-self: flex-end;
  align-items: center;
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
  margin-right: 10px;
  font-family: inherit;
  font-weight: 600;
  transition: all ease-in;
  display: flex;
  box-shadow: inset 0 0 10px #010100cc;
}

.btn1 {
  background-color: #d9d9d9;
}

.btn:active {
  border: 4px solid orange;
}

.section {
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  display: flex;
}

#WithuseState {
  background-color: #996719;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 10px;
  flex-direction: column;
  align-self: flex-end;
  gap: 20px;
  width: min(90%, 900px);
  display: flex;
}

.SecondHeading {
  align-self: flex-end;
}

.btn2 {
  background: #eac07f;
  align-self: flex-start;
  margin-left: 10px;
}

.changecolorbox2 span {
  margin-right: 10px;
  font-size: 1.5rem;
  display: block;
}

.changecolorbox2 {
  color: #f7e7ce;
  background-color: #eac07f;
  border-radius: 50px 0 0 50px;
  justify-content: flex-end;
  align-self: flex-end;
  width: 70%;
  padding: 40px 0;
  display: flex;
}

#showcode {
  flex-direction: column;
  gap: 10px;
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.code1 {
  color: #fff;
  background-color: #db1f48;
}

.code2 {
  color: #fff;
  background-color: #004369;
}

.codebtn {
  cursor: pointer;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  padding: 1em;
  font-size: clamp(.9rem, 2vw, 1.4rem);
  transition: all .2s ease-in;
}

.codebtn:hover {
  transform: scale(1.05);
}

.codebtn:hover:active {
  transform: scale(1);
}

.popup {
  color: #010100;
  text-align: center;
  background-color: #e1c340;
  border-radius: 10px;
  width: 300px;
  padding: 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.popup button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 5px 10px;
}

@media (width <= 380px) {
  .heading {
    font-size: 30px;
  }
}

@media (width <= 428px) {
  .changecolorbox1 span, .changecolorbox2 span {
    font-size: 1.2rem;
  }
}

@media (width <= 335px) {
  .changecolorbox1, .changecolorbox2 {
    width: 80%;
  }
}
